.profile {
    display: flex;
    flex-direction: column;
    align-items: center;
    font-size: 10px;
    box-shadow: 0px 0px 3px 1px rgba(185, 185, 185, 1);
    overflow: hidden;
    border-radius: 10px;
    margin: 0 auto;
}

.description {
    display: flex;
    flex-direction: column;
    align-items: center;
    background-color: white;
    padding: 30px 0;
}

.avatar {
    width: auto;
    height: 150px;
    border-radius: 50%;
    padding-bottom: 30px;
}

.name {
    font-size: 24px;
    font-weight: 700;
}

.name,
.tag {
    padding-bottom: 10px;
}

.tag,
.location {
    font-weight: 600;
    font-size: 14px;
    color: rgb(151, 163, 175);
}

.stats {
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 0 50px;
    width: 100%;
}

.item {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    border-top: 1px solid rgb(151, 163, 175);
    width: 100%;
    padding: 20px 0;
    background-color: rgb(240, 244, 247);
}

.item:first-child {
    margin-left: 0.1px;
    border-right: 1px solid rgb(151, 163, 175);
}

.item:last-child {
    margin-right: 0.1px;
    border-left: 1px solid rgb(151, 163, 175);
}

.label {
    padding-bottom: 2px;
    font-size: 12px;
    color: rgb(151, 163, 175);
    font-weight: 600;
}

.quantity {
    font-size: 16px;
    font-weight: 700;
}